import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform, AlertController, MenuController } from 'ionic-angular';
import { User } from '../../providers';



@IonicPage()
@Component({
  selector: 'page-enlace-broknet',
  templateUrl: 'enlace-broknet.html',
})

export class EnlaceBroknetPage {

  seccion: string = "datos";

  constructor(private user: User,private platform: Platform, private alertCtrl: AlertController,  public navCtrl: NavController, public menu: MenuController, public navParams: NavParams) {




  }

  ionViewDidEnter() {
    this.menu.enable(true);
  }

  create(){
    this.navCtrl.push('CreatePropertyPage');
  }

  properties() {
    this.navCtrl.push('MyPropertiesPage');
  }

  help(){

    this.navCtrl.push('HelpPage');



  }


  getPermission(){

    if(this.user.userInfo.permission == 3){
      
      return this.user.userInfo.idPermisos.includes('3')
      
    }else{
      return true;
    }

  }
}



